@charset "UTF-8";
body, html { height: 100%; font-family: Arial, Helvetica, sans-serif; }

h1 { margin-bottom: 1rem; }

.hidden { display: none; }

body { min-width: 40rem; display: grid; grid-template-rows: 8rem auto 3rem; grid-row-gap: 1rem; }

body footer { display: none; }

body#index > main, body#overview > main { display: flex; align-items: center; }

body#index > header, body#overview > header { display: flex; }

body#index > footer, body#overview > footer { display: block; }

body#channel, body#voice { grid-template-rows: auto; }

body#channel > header, body#voice > header { display: none; }

body#channel > main, body#voice > main { display: grid; grid-template-columns: 5rem 14rem auto 12rem; grid-column-gap: 1rem; }

html { box-sizing: border-box; font-size: 16px; }

*, *:before, *:after { box-sizing: inherit; }

body, h1, h2, h3, h4, h5, h6, p, ol, ul { margin: 0; padding: 0; font-weight: normal; }

ol, ul { list-style: none; }

img { max-width: 100%; height: auto; }

* { font-family: gesta, sans-serif; font-weight: 300; font-style: normal; }

strong { font-weight: bold; }

h1, h2, h3, h4, h5 { font-family: korolev-condensed, sans-serif; font-weight: 300; font-style: normal; }

/** Font utilitites  */
.color-grey { color: #212121 !important; }

.color-grey { color: #808080 !important; }

.hover { opacity: 1; }

.hover:hover { opacity: 0.9; }

.bold { font-weight: bold; }

.no-underline { text-decoration: none; }

.alert { display: flex; justify-content: center; position: absolute; top: 0; left: 0; width: 100%; height: 2rem; color: white; background-color: #D86D0F; }

.alert > * { color: white !important; margin: 0 1rem; }

.chat { display: grid; grid-template-rows: auto 8rem; }

.chat .chat--messages { margin-top: 1rem; }

.chat .chat--messages .message { margin: 0.5rem 0; }

.chat .chat--messages .message > small { color: #212121; }

.chat .chat--input { padding: 1rem; padding-top: 0.5rem; margin-bottom: 1rem; border: thin solid #808080; border-radius: 2rem; }

.chat .chat--input .topbar { border-bottom: thin solid #808080; margin-left: -1rem; margin-right: -1rem; padding-left: 1rem; padding-right: 1rem; }

.chat .chat--input .topbar span img { height: 1.5rem; }

.chat .chat--input #chatInput > textarea { width: 100%; height: 3rem; resize: none; border: none; }

div.eventselection { width: 40rem; margin: 0 auto; padding: 3rem 1rem; border-top: thick solid #0A4681; border-bottom: thick solid #D86D0F; align-self: center; }

div.eventselection h1 { margin-top: 2rem; text-align: center; }

div.eventselection p { margin: 0 auto; text-align: center; width: 50%; }

div.eventselection ul.events { display: flex; justify-content: space-around; }

div.eventselection ul.events a { text-decoration: none; }

div.eventselection ul.events .homelink { display: none; }

div.eventselection ul.events li.event { display: flex; align-items: center; height: 8rem; width: 8rem; border: thin solid #0A4681; border-radius: 50%; text-align: center; background-color: #D86D0F; }

div.eventselection ul.events li.event .event--title { width: 100%; font-size: 2rem; color: #FFFFFF; }

div.eventselection ul.events li.event.blue { background-color: #0A4681; }

#channel .sidebar--channels, #voice .sidebar--channels { width: 100%; height: 100%; border: none; border-right: solid thick #0A4681; padding: 0; }

#channel .sidebar--channels ul.events, #voice .sidebar--channels ul.events { flex-direction: column; }

#channel .sidebar--channels ul.events:first-child, #voice .sidebar--channels ul.events:first-child { margin-top: 1rem; }

#channel .sidebar--channels ul.events .homelink, #voice .sidebar--channels ul.events .homelink { position: absolute; bottom: 0; display: block; margin-top: 1rem; }

#channel .sidebar--channels ul.events .homelink .event, #voice .sidebar--channels ul.events .homelink .event { background: #808080; }

#channel .sidebar--channels ul.events .homelink > li span.event--title, #voice .sidebar--channels ul.events .homelink > li span.event--title { font-size: 2rem; }

#channel .sidebar--channels ul.events li.event, #voice .sidebar--channels ul.events li.event { width: 4rem; height: 4rem; margin-bottom: 1rem; margin-left: 0.33333rem; }

#channel .sidebar--channels ul.events li.event .event--title, #voice .sidebar--channels ul.events li.event .event--title { font-size: 1rem; }

#channel .sidebar--channels ul.events li.event.active, #voice .sidebar--channels ul.events li.event.active { border-bottom: thick solid #DC2C2C; }

form input { display: block; border-radius: 1rem; padding: 0.25rem; margin-bottom: 0.5rem; width: 100%; }

form label { font-size: 85%; }

form.form--login { width: 25rem; padding: 3rem 1rem; margin: 0 auto; border-top: thick solid #0A4681; border-bottom: thick solid #D86D0F; }

form.form--login > h1 { margin-bottom: 1rem; text-align: center; font-size: 1.5rem; }

header { display: flex; justify-content: space-between; padding: 1rem 0; border-bottom: thick solid #0A4681; }

header * { height: 100%; }

header .org--logo { margin-right: 1rem; }

header .brand--logo { margin-left: 1rem; }

footer { width: 100%; position: absolute; bottom: 0; padding: 0.5rem; border-top: thick solid #0A4681; }

footer div.footer-index { text-align: center; }

footer div.footer-index .footer--links { display: flex; justify-content: center; }

footer div.footer-index .footer--links > * { padding-left: 0.5rem; border-left: thin solid; }

.sidebar--threads { display: grid; border-right: thin solid #808080; padding-right: 1rem; }

.sidebar--threads .channel--status { background: #0A4681; color: white; text-align: center; padding: 0.5rem; padding-bottom: 0; margin-top: -0.5rem; margin-bottom: 1rem; }

.sidebar--threads .channel--status h2 { margin: 0; }

.sidebar--threads h2 { margin: 1rem 0; }

.sidebar--threads ul.voice-channels > li, .sidebar--threads ul.text-channels > li { font-weight: bold; margin-bottom: 1rem; }

.sidebar--threads ul.voice-channels > li a, .sidebar--threads ul.text-channels > li a { color: #212121; font-weight: bold; }

.sidebar--threads .channel-users * { margin-bottom: 0.25rem; margin-left: 1rem; }

.sidebar--threads .channel-users *:first-child { margin-top: -0.5rem; }

.sidebar--threads .channel-users *:last-child { margin-bottom: 1rem; }

.sidebar--threads .voice--controls { visibility: hidden; margin-top: 2rem; }

.sidebar--threads .voice--controls .controls { display: grid; grid-template-columns: repeat(4, 1fr); grid-template-rows: 4rem 1rem 4rem; justify-items: center; align-items: center; }

.sidebar--threads .voice--controls .controls hr { grid-column: span 4; grid-row: 2; width: 100%; }

.sidebar--threads .voice--controls .controls .control { height: 2rem; width: 2rem; grid-row: 3; display: flex; align-items: center; justify-content: center; border: thin solid #808080; border-radius: 0.5rem; }

.sidebar--threads .voice--controls .controls .control a { display: flex; padding: 0.25rem; }

.sidebar--threads .voice--controls .controls .control:first-child { grid-row: 1; grid-column: 4; }

body#voice .voice--controls { visibility: visible; }

.sidebar--user { background-color: #F9F9F9; border-left: thin solid #808080; padding-left: 0.5rem; }

.sidebar--user .user--note { text-align: center; margin: 1rem; background: #FFFFFF; padding: 0.5rem; border: thin solid #808080; border-radius: 0.5rem; }

.sidebar--user .users { margin: 1rem; }

body.admin .sidebar--user .user--note { display: none; }

.note-active { color: #46628D; font-weight: bold; }

.note-active:before { display: inline-block; content: '✋'; height: 1rem; width: 1rem; margin-right: 0.25rem; /* background: url('/img/ui/smile.svg') center center no-repeat; background-size: contain; */ }

div.voice { display: flex; align-items: center; justify-content: center; background-color: #212121; margin-left: -1rem; margin-right: -1rem; padding-left: 1rem; padding-right: 1rem; }

div.voice div.voice--users { display: grid; grid-template-columns: repeat(3, 1fr); grid-gap: 1rem; }

div.voice div.voice--users > div { height: 13rem; width: 20rem; background-color: purple; }

div.voice div.voice--users > div:nth-child(1) { background: url("/img/voice/1.png") center center no-repeat; background-size: contain; }

div.voice div.voice--users > div:nth-child(2) { background: url("/img/voice/2.png") center center no-repeat; background-size: contain; }

div.voice div.voice--users > div:nth-child(3) { background: url("/img/voice/3.png") center center no-repeat; background-size: contain; }

div.voice div.voice--users > div:nth-child(4) { background: url("/img/voice/4.png") center center no-repeat; background-size: contain; }

